import { Route, Switch, HashRouter } from "react-router-dom";

import Home from "./views/index/Index";
import Categorias from "./views/main-frame/MainFrame";

function Routes() {
  return (
    <HashRouter>
      <Switch>
        <Route path="/" exact>
          <Home />
        </Route>
        <Route path="/categorias">
          <Categorias />
        </Route>
      </Switch>
    </HashRouter>
  );
}

export default Routes;
