import { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import GrpcApi from "../../api/GrpcMainApi";
import ItemList from "../../components/item-list/ItemList";
import Item from "../../components/item-link/ItemLink";
import { Produto } from "../../api/data-service_pb";
interface ProdutoParams {
  id: string | undefined;
}

function Produtos() {
  const [produtos, setProdutos] = useState<Produto[]>([]);
  const [loading, setLoading] = useState(true);
  const { id } = useParams<ProdutoParams>();

  useEffect(() => {
    const api = new GrpcApi();
    if (id) {
      const idParsed = parseInt(id);
      if (isNaN(idParsed)) {
        setLoading(false);
        return;
      }

      api.getProdutos(idParsed).then((response) => {
        const filteredProdutos = response.filter((produto) =>
          produto.getVisivel()
        );
        setProdutos(filteredProdutos);
        setLoading(false);
      });
    }
  }, [id]);

  return (
    <ItemList isLoading={loading}>
      {produtos.map((produto) => {
        return (
          <Item
            key={produto.getId()}
            img={`https://api.madeiranobremoveis.com.br/assets/${produto.getIcone()}`}
            text={produto.getNome()}
            url={`/categorias/${id}/produtos/${produto.getId()}`}
          />
        );
      })}
    </ItemList>
  );
}

export default Produtos;
