import { useState, useEffect } from "react";
import { useParams } from "react-router-dom";

import Api from "../../api/GrpcMainApi";
import Slide from "../../components/slide/Slide";
import WppLogo from "../../assets/svgs/wpp.svg";
import { Produto as ProdutoAPI } from "../../api/data-service_pb";

import "./Produto.scss";

interface ProdutoParams {
  produto: string | undefined;
}

function Produto() {
  const { produto: id } = useParams<ProdutoParams>();
  const [produto, setProduto] = useState<ProdutoAPI | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const api = new Api();
    if (id) {
      const idNum = parseInt(id);
      if (!isNaN(idNum)) {
        api
          .getProduto(idNum)
          .then((produtoResponse) => {
            if (!produtoResponse.getVisivel()) return;
            setProduto(produtoResponse);
            setLoading(false);
          })
          .catch(console.error);
      }
    }
  }, [id]);

  if (loading) {
    return (
      <div className="produto-page center">
        <h2>Carregando...</h2>
      </div>
    );
  }

  if (!produto) {
    return (
      <div className="produto-page center">
        <h2>Produto no existe!</h2>
      </div>
    );
  }

  return (
    <div className="produto-page">
      <h1>{produto.getNome()}</h1>
      <Slide
        className="config-carrousel"
        images={produto
          .getFotosList()
          .map((i) => `https://api.madeiranobremoveis.com.br/assets/${i}`)}
      />
      <h2>Informaes:</h2>
      <p>{produto.getDescricao()}</p>
      <a href={whatsAppLink(produto.getNome())} className="whatsapp">
        <img src={WppLogo} alt="Whatsapp Logo" />
        <span>Mais Informaes</span>
      </a>
    </div>
  );
}

function whatsAppLink(nome: string) {
  const now = new Date().getHours();

  let comeco = "Boa noite";
  if (now >= 5 && now < 12) {
    comeco = "Bom dia";
  } else if (now >= 12 && now < 18) {
    comeco = "Boa tarde";
  }

  return (
    "https://wa.me/5511995401715/?text=" +
    encodeURI(`${comeco}, gostaria de mais informaes sobre a(o) ${nome}`)
  );
}

export default Produto;
